<template>
  <div class="post-block">
    <div class="post-header">
      <div class="post-title">
        <h1>
          <router-link v-bind:to="link" class="post-title-link">{{postTitle}}</router-link>
        </h1>
      </div>
      <div class="post-meta">
        <span class="post-time">
          <span class="post-meta-item-icon fa fa-calendar-o"></span>
          <span class="post-meta-item-text">
            Posted on
          </span>
          <time>{{ postDate }}</time>
        </span>
        <span class="post-category">
          <span class="post-meta-divder">|</span>
          <span class="post-meta-item-icon fa fa-folder-o"></span>
          <span class="post-meta-item-text">in</span>
          <span class="bottomline">
            <a href="">{{ category }}</a>
          </span>
        </span>
        <span class="post-comments-count">
          <span class="post-meta-divder">|</span>
          <span class="post-meta-item-icon fa fa-comment-o"></span>
          <span class="post-meta-item-text">Comments:</span>
          <span class="bottomline">
            <a href="">{{ comments }} Comment</a>
          </span>
        </span>
      </div>
    </div>
    <div class="post-summary" v-html="postBody"></div>
    <v-button v-bind:link="link" v-bind:text="buttonText" v-if="enableReadMore" />
  </div>
</template>

<script lang="ts">
import { computed, ref, defineComponent, SetupContext, reactive, unref, onBeforeMount } from 'vue'
import { Post, Attributes } from '../../store/state'
import Button from './Button.vue';

interface Props {
  post?: Post;
  enableReadMore: Boolean;
};

export default defineComponent({
    components: {
      "v-button": Button,
    },
    props: {
        post: {
            type: Object as () => Post,
            require: true,
        },
        enableReadMore: {
            type: Boolean,
            default: true,
            require: false,
        }
    },
    setup(props: Props, context: SetupContext) {
        const category = computed(() => {
            return '年度总结'
        });

        const comments = ref('1');

        const link = computed(() => {
            if (props.post === undefined) {
                return String;
            }

            return props.post.link;
        });

        const postDate = computed(() => {
            if (props.post === undefined) {
                return String;
            }

            const date: Date = new Date(props.post.attributes.date);
            const year:String = String(date.getFullYear());
            const month:String = String(("0" + (date.getMonth() + 1)).slice(-2));
            const day:String = String(("0" + date.getDate()).slice(-2));

            return `${year}-${month}-${day}`;
        });

        const postBody = computed(() => {
            if (props.post === undefined) {
                return String;
            }

            return props.post.body.toString();
        });

        const postTitle = computed(() => {
            if (props.post === undefined) {
              return String;
            }

            return props.post.attributes.title;
        });

        const enableReadMore = props.enableReadMore;
        const buttonText = ref("Read more »");

        return { category, postDate, comments, link, postBody, postTitle, enableReadMore, buttonText };
    }
});
</script>

