import '@fortawesome/fontawesome-free/js/fontawesome'
import '@fortawesome/fontawesome-free/js/solid'
import '@fortawesome/fontawesome-free/js/regular'
import '@fortawesome/fontawesome-free/js/brands'

interface Props {
  link?: String;
  icon?: String;
  title?: String;
  isMain?: Boolean;
}

export default {
  props: {
    link: String,
    icon: String,
    title: String,
    isMain: Boolean,
  },
  setup(props: Props) {
    const path = reactive({
      path: props.link ?? "/"
    });

    return { path };
  }
}
